import * as React from 'react';
import { useParams, useSearchParams } from 'react-router-dom';

import { useForm } from '@miq/formjs';
import { Loading } from '@miq/componentjs';
import { useRequest, Response, ResultsResponse } from '@miq/hookjs';

import StaffView from '../../Views';
import { HitListItem, THit } from '../hits';
import { LIB, TLIB } from './utils';

export default function LIBUpdateView() {
  const { libSlug } = useParams();
  const [params] = useSearchParams();
  const form = useForm<TLIB>({});

  const paramSize = [...params.keys()].length;

  const { res: hitRes, loading: hitLoading } = useRequest(() => LIB({ slug: libSlug }).hitList(params), {
    refreshDeps: [libSlug, paramSize],
    onSuccess: ({ data }) => {},
    onError: ({ response }) => {},
  });

  const { res } = useRequest(() => LIB({ slug: libSlug }).retrieve(), {
    refreshDeps: [libSlug],
    onSuccess: ({ data }) => form.setValues(data),
    onError: ({ response }) => {},
  });

  const r = new Response<TLIB>(res);
  if (!r.isSuccess) return null;

  let instance = LIB(r.data);
  const { name } = instance;

  return (
    <StaffView back="../" title={instance.name} text={`p/${name}/`}>
      <div></div>

      <div className="">
        {hitLoading ? (
          <Loading />
        ) : (
          new ResultsResponse<THit>(hitRes)?.items?.map((hit) => (
            <HitListItem hit={hit} className="mb-2" key={hit.slug} />
          ))
        )}
      </div>
    </StaffView>
  );
}
